/* Standard grid settings */
$grid-columns:      12;
$grid-gutter-width: 30px;

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px
);

@import 'bootstrap';
@import 'selectr';
@import 'contact-form';
@import 'selectr.min';

/* Variabele */
$font-primary:      'Lato', Helvetica Neue, Helvetica, Arial, sans-serif;
$dark:              #323232;

/* Generated by Glyphter (http://www.glyphter.com) on  Wed Nov 29 2017*/
@font-face {
    font-family: 'icon';
    src: url('../fonts/icons/icon.eot');
    src: url('../fonts/icons/icon.eot?#iefix') format('embedded-opentype'),
         url('../fonts/icons/icon.woff') format('woff'),
         url('../fonts/icons/icon.ttf') format('truetype'),
         url('../fonts/icons/icon.svg#icon') format('svg');
    font-weight: normal;
    font-style: normal;
}
[class*='icon-']:before{
    display: inline-block;
    font-family: 'icon';
    font-style: normal;
    font-weight: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}
.icon-facebook:before{content:'\0041';}
.icon-instagram:before{content:'\0042';}
.icon-linkedin:before{content:'\0043';}
.icon-twitter:before{content:'\0044';}
.icon-cursor:before{content:'\0045';}
.icon-check:before{content:'\0046';}
.icon-cross:before{content:'\0047';}

/* Global */
html {
    font-size: 62.5%;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
	overflow-x: hidden;
    &.not-scrollable {
        overflow: hidden;
    }
}

body {
    max-width: 100%;
    overflow-y: hidden;
    margin: 0;
    line-height: 1.5;
    font-family: $font-primary;
	font-size: 1.8rem;
	color: $dark;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding-top: 6.6rem;
}

.ptn,.pvn,.pan{padding-top:0 !important}.pts,.pvs,.pas{padding-top:0.5em !important}.ptm,.pvm,.pam{padding-top:1em !important}.ptl,.pvl,.pal{padding-top:2em !important}.prn,.phn,.pan{padding-right:0 !important}.prs,.phs,.pas{padding-right:0.5em !important}.prm,.phm,.pam{padding-right:1em !important}.prl,.phl,.pal{padding-right:2em !important}.pbn,.pvn,.pan{padding-bottom:0 !important}.pbs,.pvs,.pas{padding-bottom:0.5em !important}.pbm,.pvm,.pam{padding-bottom:1em !important}.pbl,.pvl,.pal{padding-bottom:2em !important}.pln,.phn,.pan{padding-left:0 !important}.pls,.phs,.pas{padding-left:0.5em !important}.plm,.phm,.pam{padding-left:1em !important}.pll,.phl,.pal{padding-left:2em !important}.mtn,.mvn,.man{margin-top:0 !important}.mts,.mvs,.mas{margin-top:0.5em !important}.mtm,.mvm,.mam{margin-top:1em !important}.mtl,.mvl,.mal{margin-top:2em !important}.mrn,.mhn,.man{margin-right:0 !important}.mrs,.mhs,.mas{margin-right:0.5em !important}.mrm,.mhm,.mam{margin-right:1em !important}.mrl,.mhl,.mal{margin-right:2em !important}.mbn,.mvn,.man{margin-bottom:0 !important}.mbs,.mvs,.mas{margin-bottom:0.5em !important}.mbm,.mvm,.mam{margin-bottom:1em !important}.mbl,.mvl,.mal{margin-bottom:2em !important}.mln,.mhn,.man{margin-left:0 !important}.mls,.mhs,.mas{margin-left:0.5em !important}.mlm,.mhm,.mam{margin-left:1em !important}.mll,.mhl,.mal{margin-left:2em !important}

.text-center {
    text-align: center;
}

section {
    padding: 8rem 0;
}

.coverlink {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
}

/* Loader */
.end-loading {
    text-align: center;
    color: $dark;
    font-size: 1.6rem;
}

@keyframes loader {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.loader {
    width: 100%;
}
.loading {
    display: block;
	width: 100%;
    height: 3.2rem;
	margin: 0 auto;
	position: relative;
    .loader-icon {
        display: block;
        width: 3.2rem;
        height: 3.2rem;
        margin: 0 auto;
        border: .2em solid $dark;
        border-bottom-color: transparent;
        border-radius: 50%;
        animation: .9s loader linear infinite;
        position: relative;
    }
}

/* Links & buttons */
a {
	color: $dark;
    cursor:pointer;
    text-decoration: none;
    transition: .3s all ease-in-out;
    &:hover {
        color: $dark;
        text-decoration: none;
    }
}

button:focus,
a:focus {
    outline: none !important;
    text-decoration: none;
}

/* Navbar */
.navbar {
    &--fixed-top {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        background-color: white;
        padding: 1rem 0;
        box-shadow: 0 0 .4rem 0 rgba(0,0,0,.1);
        z-index: 1029;
    }
    &__logo {

    }
    &__menu {
        text-align: right;
        ul {
            margin: 0;
            padding: 0;
            li {
                list-style: none;
                margin-left: 3.5rem;
                display: inline-block;
                position: relative;
                &.current_page_item {
                    > a {
                        color: red;
                    }
                }
                /* Start submenu */
                &:before {
                    position: absolute;
                    bottom: -34px;
                    height: 5rem;
                    background: transparent;
                    width: 100%;
                    display: block;
                    content: "";
                    top: 2rem;
                }
                &:hover {
                    > a {
                        color: red;
                    }
                    .sub-menu {
                        max-height: 50rem;
                        transition: .3s max-height ease-in;
                        li {
                            pointer-events: all;
                            a {
                                opacity: 1;
                                visibility: visible;
                                pointer-events: all;
                            }
                        }
                    }
                }
                .sub-menu {
                    background-color: #ffffff;
                    width: 200px;
                    position: absolute;
                    margin-top: 1.6rem;
                    left: -3rem;
                    text-align: left;
                    transition: .3s max-height ease-out;
                    box-shadow: 0px 2px 2px 0px rgba(0,0,0,0.1);
                    max-height: 0;
                    li {
                        position: relative;
                        margin: 0;
                        padding: .5rem 3rem;
                        display: block;
                        height: auto;
                        line-height: 1.5;
                        pointer-events: none;
                        &:last-child {
                            padding-bottom: 1rem;
                        }
                        a {
                            font-size: 1.4rem;
                            font-weight: 300;
                            display: block;
                            opacity: 0;
                            visibility: hidden;
                            &:after,
                            &:before {
                                content: none;
                            }
                        }
                    }
                }
                a {
                    color: $dark;
                }
            }
        }
    }
    $bar-width: 24px;
    $bar-height: 2px;
    $bar-spacing: 8px;
    &__hamburger {
        position: relative;
        float: right;
        width: $bar-width;
        height: $bar-height + $bar-spacing * 2;
        cursor: pointer;
    }
    &__hamburger-wrapper {
        float: right;
    }
    &__hamburger-bar {
        position: relative;
        transform: translateY($bar-spacing);
        background: $dark;
        width: $bar-width;
        height: $bar-height;
        transition: all 0s .3s;
        &:after {
            content: '';
            width: $bar-width;
            height: $bar-height;
            position: absolute;
            left: 0;
            top: $bar-spacing;
            background: $dark;
            transition: .3s .3s top cubic-bezier(0.23, 1, 0.32, 1), .3s transform cubic-bezier(0.23, 1, 0.32, 1);
        }
        &:before {
            content: '';
            width: $bar-width;
            height: $bar-height;
            position: absolute;
            left: 0;
            bottom: $bar-spacing;
            background: $dark;
            transition: .3s .3s bottom cubic-bezier(0.23, 1, 0.32, 1), .3s transform cubic-bezier(0.23, 1, 0.32, 1);
        }
    }
    &__hamburger-bar--animate {
        background: transparent;
        &:after {
            top: 0;
            transform: rotate(45deg);
            transition: .3s top cubic-bezier(0.23, 1, 0.32, 1), .3s .3s transform cubic-bezier(0.23, 1, 0.32, 1);
        }
        &:before {
            bottom: 0;
            transform: rotate(-45deg);
            transition: .3s top cubic-bezier(0.23, 1, 0.32, 1), .3s .3s transform cubic-bezier(0.23, 1, 0.32, 1);
        }
    }
}

.mobile-menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 1028;
    background-color: #FFF;
    transform: translateY(-100%);
    transition: .3s transform ease-in-out;
    &__wrapper {
        height: 100%;
        text-align: left;
        width: 100%;
        padding: 4rem;
        display: flex;
        align-items: center;
        transition: .3s transform ease-in-out;
        ul {
            margin: 0;
            width: 100%;
            padding: 0;
            li {
                list-style: none;
                display: block;
                margin: 1rem 0;
                line-height: 2;
                position: relative;
                width: 100%;
                @for $i from 0 to 5 {
                    &.mobile-animate:nth-child(#{$i}) {
                        margin-left: -2rem;
                        opacity: 0;
                        transition-delay: $i * 0.15s;
                    }
                }
                a {
                    color: $dark;
                }
                .more {
                    position: absolute;
                    top: 0;
                    right: 0;
                    line-height: 1.5;
                    i {
                        border-radius: 50%;
                        display: inline-block;
                        margin: 0 .2rem;
                        width: .4rem;
                        height: .4rem;
                        background-color: $dark;
                    }
                }
            }
        }
        ul.sub-menu {
            transition: .3s all ease-in-out;
            position: fixed;
            top: 50%;
            z-index: 999;
            display: block;
            padding-left: 0;
            width: 100%;
            text-align: left;
            opacity: 0;
            transform: translate(100%,-50%);
        }
        .back {
            a {
                color: #056873;
                font-size: 1.6rem;
            }
            i.icon-cursor {
                transform: translateY(-3px) rotate(180deg);
                display: inline-block;
                margin: 0 1rem 0 0;
                color: $dark;
                font-size: 1.2rem;
            }
        }
    }
}

/* Mobile submenu */
.sub-menu-active {
    .mobile-menu {
        &__wrapper {
            transform: translateX(-100%);
            .children-active {
                ul.sub-menu {
                    z-index: 9999;
                    opacity: 1;
                    ul.sub-menu {
                        opacity: 0;
                    }
                    &.submenu-active {
                        transform: translate(-100%, -50%);
                        li.children-active {
                            ul.sub-menu {
                                transform: translate(-200%, -50%);
                                opacity: 1;
                            }
                        }
                    }
                }
            }
        }
    }
}

.nav-open {
    .mobile-menu {
        transform: translateY(0);
        &__wrapper {
            @for $i from 0 to 5 {
                li.mobile-animate:nth-child(#{$i}) {
                    margin-left: 0;
                    opacity: 1;
                }
            }
        }
    }
}

/* POSTTYPE */
.posttype {
    &__lazyload-container {
        min-height: 80rem;
    }
    &__item {
        margin-bottom: 2rem;
        &--block {
            opacity: 0;
            transition: .3s all ease;
            &--in {
                opacity: 1;
            }
        }
    }
    &__item-wrapper {
        position: relative;
    }
    &__item-image {
        width: 100%;
        height: 30rem;
        background-size: cover;
        background-position: center center;
        position: relative;
        transition: .3s transform ease-in-out;
    }
    &__item-content {
        padding: 3rem 0;
        transition: .3s transform ease-in-out;
        h3 {
            margin: 0;
            $font-size: 2.4rem;
            $line-height: 1;
            $lines-to-show: 2;
            color: $dark;
            display: block; /* Fallback for non-webkit */
            display: -webkit-box;
            height: $font-size*$line-height*$lines-to-show; /* Fallback for non-webkit */
            font-size: $font-size;
            line-height: $line-height;
            -webkit-line-clamp: $lines-to-show;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
            word-wrap: break-word;
        }
    }
    &__filter {
        text-align: center;
        margin-bottom: 4rem;
        input {
            display: none;
        }
        input + span {
            display: inline-block;
            padding: 1rem 3rem 1rem 3rem;
            margin: 0 .5rem;
            border-radius: 0rem;
            cursor: pointer;
            color: $dark;
            border: .2rem solid $dark;
            opacity: 1;
            transition: .3s all ease-in-out;
            &:hover {

            }
        }
        input:checked + span {
            background-color: $dark;
            color: white;
            border-color: $dark;
        }
    }
}

/* FOOTER */
.footer {
    &__wrapper {

    }
    &__realized {

    }
}

@import 'responsive';
