.selectr-container {
    .selectr-selected {
        outline: none;
        border: 2px solid #f1f1f1;
        padding: 1.2rem 2rem;
        .selectr-placeholder {
            color: $dark;
        }    
    }
    &.open .selectr-selected {
        border: 2px solid #f1f1f1; 
    }
    .selectr-options-container {
        border: 2px solid #f1f1f1;
    }
    .selectr-option.selected {
        background-color: #000;
        color: #FFF;
        &:hover {
            background-color: #000;
            color: #FFF;
        }
    }
    .selectr-option.active {
        background-color: #f1f1f1;
        color: $dark;
    }
}